
const stress_symbols = {
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':''
}

export const normalWord = (word) => {
    let nWord = ''
    let word_by_letters = word.split('')
    word_by_letters.forEach(letter=>{
        let inArray_letter = stress_symbols[letter]
        if(inArray_letter){
            nWord += inArray_letter
        }else{
            nWord += letter
        }
    })

    return nWord
}